import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';
import { SplashScreen } from '@ionic-native/splash-screen';
import { StatusBar } from '@ionic-native/status-bar';
import { HttpModule } from '@angular/http';
import { MomentModule } from 'angular2-moment';
import { Geolocation } from '@ionic-native/geolocation';
import { IonicStorageModule } from '@ionic/storage';
import { HockeyApp } from 'ionic-hockeyapp';

import { MyApp } from './app.component';
import { Facebook } from '@ionic-native/facebook';
import { GooglePlus } from '@ionic-native/google-plus';
import { AuthService } from '../providers/auth-service';
import { DealsService } from '../providers/deals-service';
import { MerchantService } from '../providers/merchant-service';
import { MapProvider } from '../providers/map/map';
import { SpinnerProvider } from '../providers/spinner/spinner';
import { LaunchNavigator } from '@ionic-native/launch-navigator';
import { CallNumber } from '@ionic-native/call-number';
import { NgxQRCodeModule } from 'ngx-qrcode2';
import { CategoryService } from '../providers/category-service';
import { HttpClientModule } from '@angular/common/http';
import { Camera } from '@ionic-native/camera';
import { AngularFireModule } from 'angularfire2';
import { AngularFirestoreModule } from 'angularfire2/firestore';
import { environment as ENV } from '../environments/environment';
import { SocialSharing } from '@ionic-native/social-sharing';
import { Deeplinks } from '@ionic-native/deeplinks';
import { Base64 } from '@ionic-native/base64';
//import { NavController } from 'ionic-angular/navigation/nav-controller';

export const firebaseConfig = ENV.firebaseConfig;

@NgModule({
  declarations: [
    MyApp
  ],
  imports: [
    BrowserModule, HttpModule, MomentModule,
    NgxQRCodeModule, HttpClientModule,
    IonicModule.forRoot(MyApp, {}, 
{
  links: [
    { loadChildren: '../components/login/layout-1/login-layout-1.module.ngfactory#LoginLayout1ModuleNgFactory', name: 'LoginLayout1', segment: 'login-layout-1', priority: 'low', defaultHistory: [] },
    { loadChildren: '../components/login/layout-2/login-layout-2.module.ngfactory#LoginLayout2ModuleNgFactory', name: 'LoginLayout2', segment: 'login-layout-2', priority: 'low', defaultHistory: [] },
    { loadChildren: '../components/search-bar/layout-1/search-bar-layout-1.module.ngfactory#SearchBarLayout1ModuleNgFactory', name: 'SearchBarLayout1', segment: 'search-bar-layout-1', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/category-list/category-list.module.ngfactory#CategoryListPageModuleNgFactory', name: 'CategoryListPage', segment: 'category-list', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/claims-list/claims-list.module.ngfactory#ClaimsListPageModuleNgFactory', name: 'ClaimsListPage', segment: 'claims-list', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/deal/deal.module.ngfactory#DealPageModuleNgFactory', name: 'DealPage', segment: 'deal', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/deals-deals/deals-deals.module.ngfactory#DealsDealsPageModuleNgFactory', name: 'DealsDealsPage', segment: 'deals-deals', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/deals-list/deals-list.module.ngfactory#DealsListPageModuleNgFactory', name: 'DealsListPage', segment: 'deals-list', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/expire-deals/expire-deals.module.ngfactory#ExpireDealsPageModuleNgFactory', name: 'ExpireDealsPage', segment: 'expire-deals', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/location/location.module.ngfactory#LocationPageModuleNgFactory', name: 'LocationPage', segment: 'location', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/login/login.module.ngfactory#LoginPageModuleNgFactory', name: 'LoginPage', segment: 'login', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/merchant/merchant.module.ngfactory#MerchantPageModuleNgFactory', name: 'MerchantPage', segment: 'merchant', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/my-deals/my-deals.module.ngfactory#MyDealsPageModuleNgFactory', name: 'MyDealsPage', segment: 'my-deals', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/near-me/near-me.module.ngfactory#NearMePageModuleNgFactory', name: 'NearMePage', segment: 'near-me', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/notifications/notifications.module.ngfactory#NotificationsPageModuleNgFactory', name: 'NotificationsPage', segment: 'notifications', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/profile/profile.module.ngfactory#ProfilePageModuleNgFactory', name: 'ProfilePage', segment: 'profile', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/qr-code/qr-code.module.ngfactory#QrCodePageModuleNgFactory', name: 'QrCodePage', segment: 'qr-code', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/signup/signup.module.ngfactory#SignupPageModuleNgFactory', name: 'SignupPage', segment: 'signup', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/slider/slider.module.ngfactory#SliderPageModuleNgFactory', name: 'SliderPage', segment: 'slider', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/tabs/tabs.module.ngfactory#TabsPageModuleNgFactory', name: 'TabsPage', segment: 'tabs', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/wish-list/wish-list.module.ngfactory#WishListPageModuleNgFactory', name: 'WishListPage', segment: 'wish-list', priority: 'low', defaultHistory: [] }
  ]
}),
    AngularFireModule.initializeApp(firebaseConfig),
    AngularFirestoreModule,
    IonicStorageModule.forRoot({
      name: "__mydb",
      driverOrder: ['sqlite', 'websql', 'indexeddb']
      })
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp
  ],
  providers: [
    StatusBar, Facebook, GooglePlus, HockeyApp,
    SplashScreen, Geolocation, Base64,
    {provide: ErrorHandler, useClass: IonicErrorHandler},
    AuthService, DealsService, MerchantService,
    MapProvider, SpinnerProvider, LaunchNavigator, CallNumber,
    CategoryService, Camera, SocialSharing, Deeplinks
  ]
})
export class AppModule {}



